import React, { Component } from "react";

import moment from "moment"

//import EditableTable from "./editableTable";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { 
  Form, Input, Select, Col, InputNumber, DatePicker, 
  Divider, Tabs,
  Tooltip, Icon
} 
from "antd";
//import notification from "../../../../components/notification";
import ModalFixedAsset from "./modalFixedAsset";

// const TabPane = Tabs.TabPane;

const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;

class FixedAssetTransferCreateForm extends Component {
  state={
    id_fixed_asset: null,
    visibleModalFixedAsset: false,
  }
  
  handleModalFixedAsset = () => {
    // console.log("masuk")
    this.setState({
      visibleModalFixedAsset: !this.state.visibleModalFixedAsset
    })    
  };

  handleSelectModalFixedAsset = value => {
    const { import_fixed_assets } = this.props.fixedAssetTransfer;
    const { getFieldValue, setFieldsValue } = this.props.form;


    let [import_fixed_asset] = import_fixed_assets.filter(
      curr_data => curr_data.id_fixed_asset === value
    );

    let estimated_life = import_fixed_asset.estimated_life_year * 12 + import_fixed_asset.estimated_life_month
    let asset_age =  moment(new Date(), "YYYY-MM-DD").diff(moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD"), "months")

    // check id_to_location 
    if(getFieldValue('id_to_location') !== null && getFieldValue('id_to_location') === import_fixed_asset.id_location){
      setFieldsValue({
        id_to_location : null
      })
    }

    setFieldsValue({
      id_from_location: import_fixed_asset.id_location,
      id_department: import_fixed_asset.id_department,
      fixed_asset_code: import_fixed_asset.fixed_asset_code,
      fixed_asset_name: import_fixed_asset.fixed_asset_name,
      serial_no: import_fixed_asset.serial_no,
      asset_value_fa: parseFloat(import_fixed_asset.asset_value),
      //acquisition_date: moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD HH:mm:ss"),
      acquisition_date: moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD"),
      depreciation_end_date: moment(import_fixed_asset.depreciation_end_date, "YYYY-MM-DD"),
      asset_age : estimated_life - asset_age > 0 ? asset_age : estimated_life ,
      estimated_life,
      qty_fa: import_fixed_asset.qty,
      monthly_depreciation_fa: import_fixed_asset.monthly_depreciation,
      book_value_fa: parseFloat(import_fixed_asset.book_value),
      depreciation_value_from_fa: parseFloat(import_fixed_asset.depreciation_value),
      transfer_date: getFieldValue("transfer_date") < moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD") 
        ? moment()
        : getFieldValue("transfer_date")
    })

    this.setState({
      id_fixed_asset : value
    }) 

    //this.handleChangeQuantity(getFieldValue("qty"));
    this.handleModalFixedAsset();
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if(!err) {
        values.transfer_date = values.transfer_date.format("YYYY-MM-DD HH:mm:ss");
        values.id_fixed_asset = this.state.id_fixed_asset;
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.fixedAssetTransfer);
    const { getFieldDecorator, getFieldValue, getFieldError } = this.props.form;
    const { fixedAssetTransfer } = this.props;
    const { loading, loadingImport } = fixedAssetTransfer;
    
    let permissions = this.props.permissions;

    // const error = fixedAssetTransfer.errorField ? fixedAssetTransfer.errorField : [];
    //console.log(base_currency);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalFixedAsset
          visible={this.state.visibleModalFixedAsset}
          fixedAssetTransfer={fixedAssetTransfer}
          //id_location={this.state.id_location}
          handleModal={this.handleModalFixedAsset}
          handleSelectModal={this.handleSelectModalFixedAsset}
        />
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="fixedAssetTransfer.subtitle.fixedAsset" />
        </Divider>
        
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={
                <Tooltip
                  placement="top"
                  title={
                    <IntlMessages id="fixedAssetTransfers.import.help" />
                  }
                >
                  <IntlMessages id="fixedAssetTransfers.import" />
                  <Icon
                    style={{ marginLeft: "5px" }}
                    type="question-circle"
                    theme="filled"
                  />
                </Tooltip>
              }
            >
              <Button
                icon="select"
                loading={loadingImport}
                disabled={loadingImport}
                onClick={this.handleModalFixedAsset}
                style={{ height: "31px", width: "100%" }}
                // shape="circle"
              >
                <IntlMessages id="button.import" />
              </Button>
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_from_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      //onChange={this.handleSelectLocation}
                      style={{ width:"100%" }}
                      disabled={true}
                    >
                      {fixedAssetTransfer.from_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          { data.location_code + " (" + data.location_name +")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="departments.pageHeader" />}
                >
                  {getFieldDecorator("id_department", {
                    // initialValue: data.id_department,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width:"100%" }}
                      disabled={true}
                    >

                      { fixedAssetTransfer.departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          { data.department_code + " (" + data.department_name +")"}
                        </Option>     
                        ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssets.add.code" />}
                >
                  {getFieldDecorator("fixed_asset_code", {
                    //initialValue: data.fixed_asset_code,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssetTransfers.add.name" />}
                >
                  {getFieldDecorator("fixed_asset_name", {
                    //initialValue: data.fixed_asset_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input 
                        disabled={true} 
                      />)}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssetTransfers.add.serialNo" />}
                >
                  {getFieldDecorator("serial_no", {
                    //initialValue: data.serial_no,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input 
                      disabled={true}
                    />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetValue" />}
                >
                  {getFieldDecorator("asset_value_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      min={0}
                      precision={2}
                      disabled={true}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            

          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            
            <InputGroup style={{ marginBottom:"-5px" }}>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="acquisitionDate" />
                  }
                >
                  {getFieldDecorator("acquisition_date", {
                    //initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%", padding: "0px" }}
                      format="DD MMMM YYYY"
                      disabled={true}
                      allowClear={false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="depreciationEndDate" />
                  }
                >
                  {getFieldDecorator("depreciation_end_date", {
                    //initialValue: moment().add(12, "month"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%", padding:"0px" }}
                      format="DD MMMM YYYY"
                      allowClear={false}
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetAge" />}
                >
                  {getFieldDecorator("asset_age", {
                    //initialValue: data.fixed_asset_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="estimatedLife" />}
                >
                  {getFieldDecorator("estimated_life", {
                    //initialValue: data.estimated_life,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>     
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="quantity" />}
                >
                  {getFieldDecorator("qty_fa", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="monthlyDepreciation" />}
                >
                  {getFieldDecorator("monthly_depreciation_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      //onChange= {this.handleChangeFromEstimatedLifeYear}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>     
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="bookValue" />}
                >
                  {getFieldDecorator("book_value_fa", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accumulationOfDepreciation" />}
                >
                  {getFieldDecorator("depreciation_value_from_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}                      
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          
          </div>  
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="fixedAssetTransfer.subtitle.transfer" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup style={{ marginBottom:"-5px" }}>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="transferDate" />
                  }
                >
                  {getFieldDecorator("transfer_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      // onChange={this.handleChangeTransDate}
                      // onOpenChange={this.handleChangeTransDate}
                      disabled={
                        !permissions.includes("Can Back Date")
                          ? true
                          : false
                      }
                      allowClear={false}
                      disabledDate={e => 
                        getFieldValue("acquisition_date")
                          ? e.valueOf() > moment() || e.valueOf() < moment(getFieldValue("acquisition_date"), "YYYY-MM-DD HH:mm:ss")  
                          : e.valueOf() > moment()       
                      }
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="quantity" />}
                >
                  {getFieldDecorator("qty", {
                    initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      //disabled={true}
                      min={0.01}
                      max={getFieldValue('qty_fa') ? parseFloat(getFieldValue('qty_fa')) : 999999999999999}
                      precision={2}
                      //onChange={this.handleChangeQuantity}
                      loading={loading}
                      style={{ width:"100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fromLocation" />}
                >
                  {getFieldDecorator("id_from_location", {
                    // initialValue: data.id_from_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      //onChange={this.handleSelectLocation}
                      style={{ width:"100%" }}
                      disabled={true}
                    >
                      {fixedAssetTransfer.from_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          { data.location_code + " (" + data.location_name +")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="toLocation" />}
                >
                  {getFieldDecorator("id_to_location", {
                    // initialValue: data.id_to_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetTransfer.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width:"100%" }}
                    >

                      { fixedAssetTransfer.to_locations.map((data, key) => (
                        getFieldValue('id_from_location') !== data.id_location 
                        ?
                          (
                            <Option key={key} value={data.id_location}>
                              { data.location_code + " (" + data.location_name +")"}
                            </Option> 
                          )
                        : null
                        ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.note,
              })(<TextArea 
                  autosize={{ minRows: 4 }}
              />)}
            </Form.Item>
          </div>
        </div>    

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedFixedAssetTransferCreateForm = Form.create({ name: "create_fixed_asset" })(FixedAssetTransferCreateForm);

export default WrappedFixedAssetTransferCreateForm;
